/* http://meyerweb.com/eric/tools/css/reset/
   v5.0.1 | 20191019
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, menu, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
main, menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, main, menu, nav, section {
  display: block;
}

/* HTML5 hidden-attribute fix for newer browsers */
*[hidden] {
  display: none;
}

body {
  line-height: 1;
}

menu, ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

html {
  font-size: 62.5%;
}

body {
  font-size: 1.6rem;
}

* {
  box-sizing: border-box;
}

.srOnly {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(1px, 1px, 1px, 1px);
}

.wrapper {
  min-height: 100vh;
  font-size: 1.6rem;
  font-family: Josefin Sans, sans-serif;
  background: linear-gradient(135deg, white, #fff5f5);
}
@media (min-width: 64rem) and (orientation: landscape) {
  .wrapper {
    position: relative;
    display: grid;
    grid-template: "header  . heroImage" auto "content . heroImage" 1fr/1fr 0 42.361111%;
    background: transparent;
  }
}
@media (min-width: 75rem) {
  .wrapper {
    grid-template-columns: 1fr 3.819444% 42.361111%;
  }
}

.header {
  display: flex;
  padding: 3.1rem 3.2rem 3.2rem;
}
@media (min-width: 64rem) and (orientation: landscape) {
  .header {
    grid-area: header;
    width: 44.5rem;
    margin: auto;
    padding: 6.5rem 0;
  }
}

.logo {
  height: 2.1rem;
}
@media (min-width: 48rem) {
  .logo {
    height: 2.8rem;
  }
}
@media (min-width: 75rem) {
  .logo {
    height: 3.3rem;
  }
}

.heroImage {
  width: 100vw;
  height: 66.6667vw;
  background: url(hero-mobile.jpg) center no-repeat;
  background-size: cover;
}
@media (min-width: 64rem) and (orientation: landscape) {
  .heroImage {
    grid-area: heroImage;
    width: 100%;
    height: 100%;
    background-image: url(hero-desktop.jpg);
  }
}

.content {
  max-width: 40rem;
  margin: 0 auto;
  padding: 6.4rem 3.2rem 3.2rem;
  color: #ce9797;
  text-align: center;
}
@media (min-width: 48rem) {
  .content {
    max-width: 44.5rem;
  }
}
@media (min-width: 64rem) and (orientation: landscape) {
  .content {
    grid-area: content;
    text-align: left;
  }
}
@media (min-width: 75rem) {
  .content {
    padding: 6.5rem 0;
  }
}
.content_heading {
  margin: 0;
  font-weight: 300;
  font-size: 4rem;
  line-height: 105%;
  letter-spacing: 0.27em;
  text-transform: uppercase;
}
@media (min-width: 48rem) {
  .content_heading {
    font-size: 5.6rem;
    line-height: 110%;
  }
}
@media (min-width: 75rem) {
  .content_heading {
    font-size: 6.4rem;
  }
}
.content_heading-accent {
  color: #413a3a;
  font-weight: 600;
}
.content_text {
  margin: 1.6rem 0 0;
  font-size: 1.4rem;
  line-height: 160%;
}
@media (min-width: 48rem) {
  .content_text {
    margin-top: 2rem;
    font-size: 1.6rem;
    line-height: 175%;
    letter-spacing: 0.004em;
  }
}

@media (min-width: 64rem) and (orientation: landscape) {
  .background {
    z-index: -1;
    grid-area: 1/1/span 2/span 2;
    background: url(bg-pattern-desktop.svg) no-repeat;
    background-size: 100% 100%;
  }
}

.subscribe {
  position: relative;
  margin-top: 3.2rem;
}
@media (min-width: 48rem) {
  .subscribe {
    margin-top: 4rem;
  }
}
.subscribe_label {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(1px, 1px, 1px, 1px);
}
.subscribe_email {
  width: 100%;
  padding: 1.7rem 7.4rem 1.6rem 2.4rem;
  color: #413a3a;
  font: 1.4rem Josefin Sans, sans-serif;
  background: transparent;
  border: none;
  border-radius: 2.4rem;
  box-shadow: inset 0 0 0 0.1rem #ce9797;
  transition: 0.3s;
}
@media (min-width: 48rem) {
  .subscribe_email {
    padding: 2rem 11.6rem 2rem 3.2rem;
    font-size: 1.6rem;
    border-radius: 2.8rem;
  }
}
.subscribe_email::placeholder {
  color: #ce9797;
}
.subscribe_email:focus {
  outline: none;
  box-shadow: inset 0 0 0 0.2rem #ce9797;
}
.subscribe_errorIcon {
  position: absolute;
  top: 1.4rem;
  right: 7.4rem;
  width: 0;
  height: 2rem;
  background: url(icon-error.svg) left no-repeat;
  background-size: contain;
  opacity: 0;
  transition: all 0.3s;
}
@media (min-width: 48rem) {
  .subscribe_errorIcon {
    top: 1.6rem;
    right: 11.6rem;
    height: 2.4rem;
  }
}
.subscribe_submit {
  position: absolute;
  top: 0;
  right: 0;
  width: 6.4rem;
  height: 4.8rem;
  padding: 0;
  background: url(icon-arrow.svg) center no-repeat, linear-gradient(135deg, #f8bfbf, #ee8c8c);
  border: none;
  border-radius: 2.4rem;
  box-shadow: 0 1rem 1.4rem rgba(206, 151, 151, 0.5);
}
@media (min-width: 48rem) {
  .subscribe_submit {
    width: 10rem;
    height: 5.6rem;
    border-radius: 2.8rem;
  }
}
.subscribe_submit:hover, .subscribe_submit:focus {
  background: url(icon-arrow.svg) center no-repeat, linear-gradient(135deg, #fcdede, #fac7c7);
  outline: none;
  box-shadow: 0 1.6rem 1.4rem rgba(206, 151, 151, 0.5);
}
.subscribe_submit:active {
  background: url(icon-arrow.svg) center no-repeat, linear-gradient(135deg, #f5a8a8, #ea7575);
  box-shadow: 0 1rem 1.4rem rgba(206, 151, 151, 0.5);
}
.subscribe_feedback {
  padding: 0.8rem 2.4rem;
  color: transparent;
  font-size: 1.3rem;
  text-align: left;
  transition: color 0.3s;
}
@media (min-width: 48rem) {
  .subscribe_feedback {
    padding: 1.1rem 3.2rem;
  }
}
.subscribe-error .subscribe_email {
  padding-right: 10.4rem;
  box-shadow: inset 0 0 0 0.1rem #f96262;
}
.subscribe-error .subscribe_email:focus {
  box-shadow: inset 0 0 0 0.2rem #f96262;
}
@media (min-width: 48rem) {
  .subscribe-error .subscribe_email {
    padding-right: 15.6rem;
  }
}
.subscribe-error .subscribe_errorIcon {
  width: 2rem;
  opacity: 1;
}
@media (min-width: 48rem) {
  .subscribe-error .subscribe_errorIcon {
    width: 2.4rem;
  }
}
.subscribe-error .subscribe_feedback {
  color: #f96262;
}
.subscribe-success .subscribe_feedback {
  color: #14b814;
}